import React from "react"
import Container from "../components/container"

export default ({data}) =>
  <Container>
    <h1>About me</h1>
    <h2>{data.site.siteMetadata.title}</h2>
    <p>I’m good enough, I’m smart enough, and gosh darn it, people like me!</p>
  </Container>

export const query = graphql`
query LayoutQuery {
  site {
    siteMetadata {
      title
    }
  }
}`